import React, { Component } from 'react'
import axios from 'axios'
import './index.css'
import { importSpecifier } from '@babel/types';

class Contact extends Component {

    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            subject: '',
            message: '',
            result: 'hello'
        }
    }

    handlechangeall = (event) => {
        this.setState({ [event.target.name]: event.target.value })
    }

    handleform = (event) => {
        event.preventDefault();
        console.log(this.state);

        let formData = new FormData();
        formData.append('name', this.state.name)
        formData.append('email', this.state.email)
        formData.append('subject', this.state.subject)
        formData.append('message', this.state.message)

        axios.post('https://rajeshroyal.com/sendmail/sendmail.php', formData)
            .then(function (response){
                document.getElementById('status').innerHTML = response.data;
                console.log(response);
                //this.setState.result = response.data;
            })
            .catch(function (response){
                console.log(response)
            });
    }




    render() {
        return (
            <div className="container-fluid">

                <section className="mb-4">
                    {/*Section: Contact v.2*/}
                    {/*Section heading*/}
                    <h2 className="h1-responsive font-weight-bold text-center my-4">Contact Me</h2>
                    {/*Section description*/}
                    <p className="text-center w-responsive mx-auto mb-5">Do you have any questions? Please do not hesitate to contact Me directly. Our team will come back to you within
        a matter of hours to help you.</p>

                    <div className="row">

                        {/*Grid column*/}
                        <div className="col-md-9 mb-md-0 mb-5">
                            <form id="contact-form" name="contact-form" onSubmit={this.handleform} method="post">

                                {/*Grid row*/}
                                <div className="row">

                                    {/*Grid column*/}
                                    <div className="col-md-6">
                                        <div className="md-form mb-5">
                                            <input value={this.state.name} placeholder="Your Name" type="text" id="name" name="name" className="form-control" onChange={this.handlechangeall} />

                                        </div>
                                    </div>
                                    {/*Grid column*/}

                                    {/*Grid column*/}
                                    <div className="col-md-6">
                                        <div className="md-form mb-5">
                                            <input value={this.state.email} placeholder="Your Email" type="text" id="email" name="email" className="form-control" onChange={this.handlechangeall} />

                                        </div>
                                    </div>
                                    {/*Grid column*/}

                                </div>
                                {/*Grid row*/}

                                {/*Grid row*/}
                                <div className="row">
                                    <div className="col-md-12">
                                        <div className="md-form mb-5">
                                            <input value={this.state.subject} placeholder="Your Subject" type="text" id="subject" name="subject" className="form-control" onChange={this.handlechangeall} />

                                        </div>
                                    </div>
                                </div>
                                {/*Grid row*/}

                                {/*Grid row*/}
                                <div className="row">

                                    {/*Grid column*/}
                                    <div className="col-md-12">

                                        <div className="md-form">
                                            <textarea value={this.state.message} placeholder="Your Message" type="text" id="message" name="message" rows="8" className="form-control md-textarea" onChange={this.handlechangeall}></textarea>
                                        </div>

                                    </div>
                                </div>
                                <div className="text-center text-md-left mb-3">
                                    <input className="btn btn-primary btn-block" type="submit" value="Send"></input>
                                </div>
                                <div id="status"></div>
                                {/*Grid row   form end*/}

                            </form>


                        </div>
                        {/*Grid column*/}

                        {/*Grid column*/}
                        <div className="col-md-3 text-center">
                            <ul className="list-unstyled mb-0">
                                <li className="borderleft"><i className="fa fa-map-marker fa-2x"></i>
                                    <p>Ajmer, Raj 305002, USA</p>
                                </li>

                                <li className="bordertop borderleft"><i className="fa fa-phone mt-4 fa-2x mt-5 pt-4"></i>
                                    <p>+91 8209 2069 12</p>
                                </li>

                                <li className="bordertop borderleft"><i className="fa fa-envelope mt-5 fa-2x pt-5"></i>
                                    <p>rajeshroyal896@gmail.com</p>
                                </li>
                            </ul>
                        </div>
                        {/*Grid column*/}

                    </div>

                </section>
                {/*Section: Contact v.2*/}
            </div>
        )
    }
}

export default Contact;